import Vue from 'vue'
import Router from 'vue-router'

import Home from './views/Home.vue'
import Login from './views/Login.vue'
import Category from './views/Category.vue'
import Cart from './views/Cart.vue'
import User from './views/User.vue'
import Product from './views/Product.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      meta: {
        title: '',
        index: 0
      }
    },
    {
      path: '/category',
      name: 'Category',
      component: Category,
      meta: {
        title: '',
        index: 1
      }
    },
    {
      path: '/cart',
      name: 'Cart',
      component: Cart,
      meta: {
        title: '',
        index: 2
      }
    },
    {
      path: '/user',
      name: 'User',
      component: User,
      meta: {
        title: '',
        index: 3
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        title: '',
        index: 4
      }
    },
    {
      path: '/product/:id',
      name: 'product',
      component: Product,
      meta: {
        title: '',
        index: 5
      }
    }
  ]
})

export default router
